.heroContainer {
    /* background-color: var(--color-gray-06); */
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    padding: 1.5rem 1rem;
    gap: 1rem;
    background-size: 400% 400%;
    background-image: linear-gradient(45deg, #f78b8b, #ffb5b5, rgb(255, 224, 224), var(--color-gray-07), #ffb5b5, #f78b8b);
    animation: gradientAnimation 15s ease infinite;
}

.heroTextWrapper {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.heroButtonWrapper {
    display: flex;
    padding-top: 1rem;
}

.heroButtonWrapper button {
    width: max-content;
}

.heroImageWrapper{
    /* background-color: aqua; */
    display: grid;
    grid-template-columns: .6fr 1fr;
}
.image0, .image1 {
    height: auto;
    width: auto;
    max-width: 35rem;
}

.image0 img, .image1 img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}



@media (min-width: 768px) {
    .heroContainer{
        display: grid;
        grid-template-columns: repeat(12,1fr);
        grid-template-rows: 1fr;
    
    }
    .heroTextWrapper{
        grid-column: 1/8;
    }
    .heroImageWrapper{
        grid-column: 8/-1;
    }
    .image0, .image1 {
       
        max-width: 50rem;
    }
